import { isWx } from './validate'
import axios from 'axios'

// sdk
let PromiseSDK
function loadWechatJSSDK() {
  if (PromiseSDK) {
    return PromiseSDK
  }
  // ,; 
  PromiseSDK = new Promise((resolve, reject) => {
    const loaded = typeof wx !== 'undefined'
    if (loaded) {
      resolve()
      return
    }
    const script = document.createElement('script')
    script.src = '//res.wx.qq.com/open/js/jweixin-1.4.0.js '
    script.onload = resolve
    script.onerror = reject
    document.head.appendChild(script)
  })
  return PromiseSDK
}

// 
async function getConfig() {
  const href = window.location.href.split('#')[0]
  const { data } = await axios({
    url: `//www.luzhongkuan.cn/api2/wx/shareConfig?url=${encodeURIComponent(
      href
    )}`
  })
  if (data.success) {
    return data.data
  }
  return Promise(Error(''))
}

// 
export default async (userShareData, userConfig) => {
  if (!isWx) return
  await loadWechatJSSDK()
  const defaultShare = {
    title: document.title, // 
    desc: '...', // 
    link: window.location.href, // JS
    imgUrl: 'http://lib.luzhongkuan.cn/images/logo.png' // 
  }
  // debug: true, // ,apialertpclogpc
  // appId: '', // 
  // timestamp: , // 
  // nonceStr: '', // 
  // signature: '',// 
  // jsApiList: [] // JS
  let config = userConfig || (await getConfig())
  wx.config(config)
  wx.ready(() => {
    const shareData = { ...defaultShare, ...userShareData }
    // QQ
    wx.updateAppMessageShareData(shareData)
    const timelineData = {
      title: shareData.desc || shareData.title, // 
      link: shareData.link, // JS
      imgUrl: shareData.imgUrl, // 
    }
    // QQ1.4.0
    wx.updateTimelineShareData(timelineData)
  })
}
