import dayjs from './day'

/**
 * @desc 
 * @param {*} str
 * @returns {number}
 */
export function hidePhone(phone = '') {
  return phone.toString().replace(/(\d{3})\d{4}(\d{4})/, "$1****$2")
}

/**
 * @desc 
 * @param {*} str
 * @returns {number}
 */
export function byteSize(str) {
  return new Blob([str]).size
}

/**
 * @desc htmlxs
 * @params str {string}
 * @returns {string}
 */
export function htmlEncode(str) {
  return str
    .replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#x27;')
    .replace(/\//g, '&#x2f;')
}

/**
 * @desc 
 * @param num {string || number}
 * @returns {string}
 */
export function formatCurrency(num) {
  return (+num || 0).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

/**
 * @desc   ${startTime}
 * @param  {Date} startTime
 * @return {String}
 */
export function timeAgo(startTime) {
  const currentTime = (new Date()).getTime()
  const time = currentTime - dayjs.toDate(startTime)
  const day = Math.floor(time / (1000 * 60 * 60 * 24))
  const hour = Math.floor(time / (1000 * 60 * 60))
  const min = Math.floor(time / (1000 * 60))
  const month = Math.floor(day / 30)
  const year = Math.floor(month / 12)
  if (year) return year + ''
  if (month) return month + ''
  if (day) return day + ''
  if (hour) return hour + ''
  if (min) return min + ''
  else return ''
}

/**
 *
 * @desc   ${endTime}
 * @param  {Date} endTime
 * @return {String}
 */
export function remainTime(endTime) {
  const startDate = new Date() // 
  const endDate = new Date(endTime) // 
  const t = endDate.getTime() - startDate.getTime() // 
  let d = 0
  let h = 0
  let m = 0
  let s = 0
  if (t >= 0) {
    d = Math.floor(t / 1000 / 3600 / 24)
    h = Math.floor((t / 1000 / 60 / 60) % 24)
    m = Math.floor((t / 1000 / 60) % 60)
    s = Math.floor((t / 1000) % 60)
  }
  return d + ' ' + h + ' ' + m + ' ' + s + ''
}
