<template>
  <div class="test-item">
    <h2>cookie</h2>
    <input type="text" v-model="text">
    <button @click="save"></button>
    <button @click="get"></button>
    <button @click="remove"></button>

  </div>
</template>

<script>
import cookieUtil from '../src/cookieUtil'

const key = 'aa'

export default {
  data() {
    return {
      text: ''
    }
  },
  methods: {
    save() {
      cookieUtil.set(key, this.text || '')
    },
    get() {
      this.text = cookieUtil.get(key)
    },
    remove() {
      cookieUtil.remove(key)
    }
  }
}
</script>

